<template>
  <v-container fill-height>
    <v-layout
      text-xs-center
      wrap
      fill-height
      column
    >
      <v-flex mb-3>
        <iframe src="http://pptplus.co.kr" name="myframe" frameborder=”0″ width="100%" height="100%" marginwidth="0" marginheight="0" :onLoad="resizeIF(this)"></iframe>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data: () => ({
  }),
  mounted(){
    this.$store.dispatch('route','/1_iframe');
  },
  methods:{
    resizeIF(Id) {
      var obj = document.getElementById(Id);
      var Body;
      var H, Min;

      // 최소 높이 설정 (너무 작아지는 것을 방지)
      Min = 200;

      // DOM 객체 할당
      try {
        if (!document.all && obj.contentWindow.document.location.href =='about:blank') {
          setTimeout("resizeIF('" + Id + "')", 10);
          return;
        }

        Body = obj.contentWindow.document.getElementsByTagName('BODY');
        Body = Body[0];

        if (this.Location != obj.contentWindow.document.location.href) {
          H = Body.scrollHeight + 5;
          obj.style.height = (H < Min ? Min : H) + 'px';

          this.Location = obj.contentWindow.document.location.href;
        }
      } catch (e) {
        setTimeout("resizeIF('" + Id + "')", 10);
        return;
      }
      setTimeout("resizeIF('" + Id + "')", 100);
    }
  }
}
</script>

